/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState, useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import useRefresh from './useRefresh'
import { useKyudoStaking } from './useContract'
import multicall from '../utils/multicall'
import stakingAbi from '../config/abi/Town.json'
import contracts from '../config/constants/contracts'

export const useStakingStatus = (forceUpdate = false) => {
  const [totalMaleStaked, setTotalMaleStaked] = useState(0)
  const [totalFemaleStaked, setTotalFemaleStaked] = useState(0)
  const [totalRewardClaimed, setTotalRewardClaimed] = useState(new BigNumber(0))
  const [lastRewardTime, setLastRewardTime] = useState(0)
  const [accBowPerMale, setaccBowPerMale] = useState(new BigNumber(0))
  const [bowPerSecond, setbowPerSecond] = useState(new BigNumber(0))
  const [accBowPerFemale, setaccBowPerFemale] = useState(new BigNumber(0))

  const { slowRefresh } = useRefresh()

  const calls = [
    { address: contracts.kyudoStaking[1116], name: 'totalMaleStaked', params: [] },
    { address: contracts.kyudoStaking[1116], name: 'totalFemaleStaked', params: [] },
    {
      address: contracts.kyudoStaking[1116],
      name: 'totalRewardClaimed',
      params: [],
    },
    { address: contracts.kyudoStaking[1116], name: 'lastRewardTime', params: [] },
    { address: contracts.kyudoStaking[1116], name: 'accBowPerMale', params: [] },
    { address: contracts.kyudoStaking[1116], name: 'bowPerSecond', params: [] },
    { address: contracts.kyudoStaking[1116], name: 'accBowPerFemale', params: [] },
  ]

  useEffect(() => {
    const fetchGlobalData = async () => {
      try {
        const data = await multicall(stakingAbi, calls)
        setTotalMaleStaked(data[0][0].toString())
        setTotalFemaleStaked(data[1][0].toString())
        setTotalRewardClaimed(new BigNumber(data[2][0].toString()))
        setLastRewardTime(data[3][0].toString())
        setaccBowPerMale(new BigNumber(data[4][0].toString()))
        setbowPerSecond(new BigNumber(data[5][0].toString()))
        setaccBowPerFemale(new BigNumber(data[6][0].toString()))
      } catch (e) {
        console.error('Fetching global data had error', e)
      }
    }

    fetchGlobalData()
  }, [slowRefresh, forceUpdate])

  return {
    totalMaleStaked,
    totalFemaleStaked,
    totalRewardClaimed,
    lastRewardTime,
    accBowPerMale,
    bowPerSecond,
    accBowPerFemale,
  }
}

export const useStakedTokens = (forceUpdate = false) => {
  const { account } = useWeb3React()
  const { fastRefresh } = useRefresh()
  const staking = useKyudoStaking()

  const [maleStake, setMaleStake] = useState([])
  const [femaleStake, setFemaleStake] = useState([])

  useEffect(() => {
    const fetchGlobalData = async () => {
      if (!account) {
        setMaleStake([])
        setFemaleStake([])
        return
      }

      try {
        const [_maleStake, _femaleStake] = await Promise.all([
          staking.methods.getAccountMales(account).call(),
          staking.methods.getAccountFemales(account).call(),
        ])

        setMaleStake(_maleStake)
        setFemaleStake(_femaleStake)
      } catch (e) {
        console.error('Fetching global data had error', e)
      }
    }

    if (staking) {
      fetchGlobalData()
    }
  }, [staking, account, fastRefresh, forceUpdate])

  return {
    maleStake,
    femaleStake,
  }
}

export const useAddTokensToStake = () => {
  const [pending, setPending] = useState(false)
  const { account } = useWeb3React()
  const contract = useKyudoStaking()

  const handleAddTokensToStake = useCallback(
    async (tokenIds) => {
      if (!account) {
        return false
      }

      try {
        setPending(true)
        const tx = await contract.methods.addTokensToStake(account, tokenIds).send({
          from: account,
        })
        setPending(false)
        return tx
      } catch (e) {
        // console.log("Stake had error :>> ", e);
        setPending(false)
        return false
      }
    },
    [account, contract],
  )

  return { handleAddTokensToStake, pending }
}

export const useClaimFromStake = () => {
  const [pending, setPending] = useState(false)
  const { account } = useWeb3React()
  const contract = useKyudoStaking()

  const handleClaim = useCallback(
    async (tokenIds, unstake) => {
      if (!account) {
        return false
      }

      try {
        setPending(true)
        const tx = await contract.methods.claimFromStake(tokenIds, unstake).send({
          from: account,
        })
        setPending(false)
        return tx
      } catch (e) {
        // console.log("Claim had error :>> ", e);
        setPending(false)
        return false
      }
    },
    [account, contract],
  )

  return { handleClaim, pending }
}
