import { useEffect, useState, useCallback, useMemo } from 'react'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import useRefresh from './useRefresh'
import { useKyudoNft } from './useContract'
import nftAbi from '../config/abi/NFT.json'
import contracts from '../config/constants/contracts'
import multicall from '../utils/multicall'

export const useNFTData = (forceUpdate = false) => {
  const [saleIsEnabled, setSaleIsEnabled] = useState(0)
  const [totalSupply, setTotalSupply] = useState(0)
  const [maxMint, setMaxMint] = useState(10)
  const [mintPrice, setMintPrice] = useState(new BigNumber(0))
  const [femaleMinted, setFemaleMinted] = useState(0)

  const { fastRefresh } = useRefresh()

  const calls = useMemo(
    () => [
      { address: contracts.kyudoNft[1116], name: 'saleIsEnabled', params: [] }, // 0
      { address: contracts.kyudoNft[1116], name: 'totalSupply', params: [] }, // 1
      { address: contracts.kyudoNft[1116], name: 'maxMint', params: [] }, // 2
      { address: contracts.kyudoNft[1116], name: 'mintPrice', params: [] }, // 3
      { address: contracts.kyudoNft[1116], name: 'femaleMinted', params: [] }, // 4
    ],
    [],
  )

  useEffect(() => {
    const fetchGlobalData = async () => {
      try {
        const data = await multicall(nftAbi, calls)
        setSaleIsEnabled(data[0][0])
        setTotalSupply(data[1][0].toNumber() - 1)
        setMaxMint(data[2][0])
        setMintPrice(new BigNumber(data[3][0].toString()))
        setFemaleMinted(data[4][0])
      } catch (e) {
        console.error('Fetching nft data had error', e)
      }
    }

    fetchGlobalData()
  }, [fastRefresh, forceUpdate, calls])

  return {
    femaleMinted,
    maxMint,
    saleIsEnabled,
    mintPrice,
    totalSupply,
  }
}

export const useMyKyudo = (forceUpdate = false) => {
  const [myKyudos, setMyKyudos] = useState([])

  const { fastRefresh } = useRefresh()
  const contract = useKyudoNft()
  const { account } = useWeb3React()

  useEffect(() => {
    const fetchGlobalData = async () => {
      try {
        if (account) {
          const _tokenIds = await contract.methods.tokensOfOwner(account).call()
          if (_tokenIds.length) {
            const calls = _tokenIds.map((id) => ({
              address: contracts.kyudoNft[1116],
              name: 'isFemale',
              params: [id],
            }))
            const res = await multicall(nftAbi, calls)
            setMyKyudos(
              _tokenIds.map((id, index) => ({
                tokenId: id,
                isFemale: res[index][0],
              })),
            )
          } else {
            setMyKyudos([])
          }
        } else {
          setMyKyudos([])
        }
      } catch (e) {
        console.error('Fetching global data had error', e)
      }
    }

    fetchGlobalData()
  }, [contract, account, fastRefresh, forceUpdate])

  return {
    myKyudos,
  }
}

export const useMintCallback = () => {
  const [pending, setPending] = useState(false)
  const { account } = useWeb3React()
  const contract = useKyudoNft()

  const handleMint = useCallback(
    async (amount, stake, value) => {
      try {
        setPending(true)
        const tx = await contract.methods.mint(amount, stake).send({
          from: account,
          value,
        })
        setPending(false)
        return tx
      } catch (e) {
        // console.log("Mint had error :>> ", e);
        setPending(false)
        return false
      }
    },
    [account, contract],
  )

  return { handleMint, pending }
}
